.myForm{
    display: flex;
    flex-direction: column;
    margin: 10% 30%;
    border: 2px solid lightblue;
    background-color: rgb(75, 171, 215);
    border-radius: 10px;
    padding: 50px 120px;

}
.myForm select {
    border-radius: 5px;
    padding: 7px;
}
.myForm input {
    padding: 5px;
    border: 0px solid rgb(157, 33, 33);
    border-radius: 5px;
}