.myform{
    display: flex;
    flex-direction: column;
    margin: 9% 30%;
    border: 2px solid lightblue;
    background-color: rgb(75, 171, 215);
    border-radius: 10px;
    padding: 50px 120px;
    text-align: center;

}
.myform select {
    border-radius: 5px;
    padding: 7px;
}
.myform input {
    padding: 5px;
    border: 0px solid white;
    border-radius: 5px;
}