.myForm{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding: 40px 120px;
    margin: 9% 30%;
    background-color: rgb(75, 171, 215);
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);

}
.formContainer{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: #fff;
    border-radius: 10px;
    margin: 60px;
}
.myForm input{
    padding: 5px;
    margin: 10px;
    border-radius: 5px;
}
.radioGroup{
    display: flex;
    align-items: first baseline;
}